
import Confirm from 'vux/src/components/confirm/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {Decimal} from 'decimal.js'
import {exchangeList, exchangeChanel} from '@/modules/exchange/api/get_exchange'
import {showloadings, hideloadings, showToasts, submit, unsubmit} from '@/utils/load'

export default {
name: 'open-orders',
data() {
return {
buy: 'buy',
exchangeListData: [],
resetList: false,    // 列表是否需要刷新,true刷新,false不刷新
state: 'open'
}
},
props: {
disabled: Boolean,
submited: Boolean
},
created() {
this.getExchangeList(this.state)
},
mounted() {
this.resetList = this.submited
},
methods: {

getExchangeList(state) {
showloadings()
exchangeList(state).then(res => {
if (res.status === 200) {
const data = res.data
if (data.code === '200') {
this.exchangeListData = data.data
hideloadings()
}
}
})
},

getChanel(uid) {
exchangeChanel(uid)
.then(res => {
if (res.status === 200) {
const data = res.data
console.log(data)
if (data.code === '200') {

hideloadings()
showToasts(submit, 'success')
this.$emit('submiting', event || window.event)
} else {

hideloadings()
showToasts(unsubmit, 'warn')
}
} else {

hideloadings()
showToasts(unsubmit, 'warn')
}
})
.catch(err => {
console.log(err)




})
},

showPlugin(id) {
const _this = this
this.$vux.confirm.show({
title: '操作提示',
content: '确认撤销当前委托',
onCancel() {
},
onConfirm() {
_this.getChanel(id)
showloadings()
}
})
},

onItemClick(id) {
this.showPlugin(id)
}
},
filters: {
toUpper(val) {
return val.toUpperCase()
},
strIntercept(val) {
return val.substr(5, 14)
},
numberIntercept(val) {
return new Decimal(val).toFixed(6)
}
},
watch: {
submited() {
this.resetList = this.submited
},
resetList() {
if (this.resetList) {
this.getExchangeList(this.state)
this.$emit('reset', event)

}
}
},
components: {
Confirm,
XButton,
Loading,
Toast
}
}



